.form-contact {
  margin-top: 0;
  margin-bottom: 40px;
  background-color: #eee;
  h2 {
    margin-left: 14px;
    font-size: 34px;
    font-weight: bold;
    margin-top: 36px;
  }
  .form-control {
    -webkit-box-shadow: none;
    -ms-box-shadow: none;
    -o-box-shadow: none;
    box-shadow: none;
    border: none;
    font-size: 16px !important;
    font-weight: 300;
    background: #fff;
    border-radius: 0px;
    &:focus,
    &:active {
      -webkit-box-shadow: none;
      box-shadow: none;
      border: 1px solid rgba(22, 173, 55, 0.8);
    }
  }

  input[type="text"] {
    height: 50px;
  }
  .button {
    position: relative;
    top: 20px;
    left: 13px;
    font-size: 16px;
    margin-bottom: 50px;
    border-radius: 0;
  }
}
