.subscribe {
  margin: 100px 0 100px 0;
  background-color: rgb(54, 53, 53);
  .form-inline {
    margin-bottom: 70px;

    .email {
      float: left;
      border-radius: 0;
      margin-right: -5px;
      width: 286px;
    }
    input[type="text"] {
      height: 46px;
    }

    .btn.btn-danger {
      float: left !important;
      padding: 10px 16px;
      font-size: 18px;
      line-height: 1.3333333;
      border-radius: 0;
    }
  }
  h2 {
    font-weight: bold;
    color: #fff;
    margin-top: 70px;
  }
  p {
    margin-bottom: 50px;
    color: #eee;
  }
}
